.loading.cover-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.loading.cover-page {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  display: -webkit-flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
}
.loading .ant-spin {
  color: #3e79f7;
}

.ant-table-column-title {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.menu-bar {
  right: 24px;
  z-index: 1000;
}
.ant-drawer-body {
  padding: 0px !important;
}
.ant-drawer.ant-drawer-open .ant-drawer-mask {
  opacity: 0 !important;
}
.ant-drawer-header,
.ant-drawer-header-title {
  justify-content: flex-end;
}
.ant-drawer-header {
  padding: 16px 0px !important;
}
.font-weight-semibold {
  overflow-wrap: break-word;
}

.ant-table-tbody > tr > td {
  padding: 0px 0px 0px 8px !important;
  height: 50px;
}

.ant-avatar {
  background-color: #aea7a7 !important;
}

.profile-text-color {
  color: #707a88;
}

.ant-table-filter-column > .ant-table-filter-trigger.active {
  color: white;
  background-color: #a1b8f4;
}

.ant-table.ant-table-small .ant-table-tbody .ant-table-wrapper:only-child .ant-table {
  margin: 10px 50px !important;
  border: 1px solid #b7b7b7;
}

.expand-parent {
  background-color: #e6eeff;
}

.ant-modal-title {
  font-size: 20px;
}

.highlight-class {
  background-color: #e6eeff;
}

.table-row-highlight {
  background-color: #ffccc7 !important;
}
.table-row-highlight-1 {
  background-color: #fff0c7 !important;
}
.ant-popover-inner-content {
  width: 400px !important;
}
.ant-page-header-heading-sub-title {
  margin-top: 5px !important;
}

.ant-drawer-content {
  z-index: 1030 !important;
}
@media only screen and (max-width: 576px) {
  .ant-pagination-options {
    display: inherit !important;
  }
}
.ant-upload-list {
  margin-top: 10px !important;
}
.downlaod-button {
  border: 1px solid gray;
  font-size: 1.1rem;
  padding: 9px;
  border-radius: 8px;
}
.floating-buttons {
  z-index: 999 !important;
  position: absolute;
  left: 0px;
}
.floating-btn {
  /* top: 35px !important; */
}
.ant-affix {
  z-index: 999 !important;
  top: 70px !important;
}
.ant-drawer-content-wrapper {
  z-index: 1100 !important;
}
.ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
  display: none !important;
}

.ant-form-item-label > label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::after {
  display: inline-block;
  color: #ff6b72;
  font-size: 14px;
  font-family: SimSun, sans-serif;
  line-height: 1;
  content: "*";
}
.floating-btn-circle {
  display: flex !important;
  align-items: center;
  min-width: 20px;
  text-align: center !important;
  border-radius: 0px 20px 20px 0px !important;
  height: 32px !important;
  padding: 4px !important;
  background: rgb(24, 144, 255) !important;
  color: #fff !important;
}
.ant-tabs-content {
  border-left: 1px solid rgb(230, 235, 241);
  border-right: 1px solid rgb(230, 235, 241);
  border-bottom: 1px solid rgb(230, 235, 241);
  border-radius: 0px 0px 12px 12px;
  background-color: #fff;
}

.ant-tabs-bottom > .ant-tabs-nav,
.ant-tabs-bottom > div > .ant-tabs-nav,
.ant-tabs-top > .ant-tabs-nav,
.ant-tabs-top > div > .ant-tabs-nav {
  margin: 0px;
}
.ant-table-tbody .ant-table-row:nth-child(even) {
  background-color: #eef5ff;
}
.ant-table-tbody > tr.ant-table-row:hover > td,
.ant-table-tbody > tr > td.ant-table-cell-row-hover {
  background-color: #ececec;
}

@media screen and (max-width: 480px) {
  .is-mobile {
    position: absolute;
    left: 0px;
    top: 25%;
  }
}
@media screen and (min-width: 768px) {
  .is-mobile {
    position: absolute;
    left: 0px;
    top: 10%;
  }
}
@media screen and (min-width: 1024px) {
  .is-mobile {
    position: absolute;
    left: 0px;
    top: 10%;
  }
}
